.heartbeat.red {
    background: red;
  -webkit-animation: heartbeat-red 1.4s linear infinite;
  -moz-animation: heartbeat-red 1.4s linear infinite;
  animation: heartbeat-red 1.4s linear infinite;
}


.timeselect {
    position: relative;
    display: block;
    padding:  0 60px 0 40px;
    margin-top: 8px;
    span {
        position: absolute;
        left: 0;
        line-height: 38px;
        top: 0;
    }

    select {
        position: absolute;
        right: 0;
        width: 60px;
        top: 0;
    }

    input {
        display: inline-block;
        width: 100%;
        top: 0;
    }
}